.valet {
  z-index: 9999;
  font-size: $font-size;
  position: fixed;
  overflow: hidden;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  height: 100vh;
  box-shadow: 0 0 0 rgba(0,0,0,0);
  background-color: rgba($black-color, 0.33);

  * {
    box-sizing: border-box;
  }

  // when Valet is active
  &.open {
    width: 100%;

    .valet-inner{
      opacity: 1;
      width: 100%;
      box-shadow: 0 0 12px rgba(0,0,0,0.33);

      @include medium-device-size{
        width: 50%;
      }

      @include large-device-size{
        width: 33%;
      }

      @include xlarge-device-size{
        width: 25%;
      }
    }
  }

  // both panels
  &.left,
  &.right{

    .valet-inner{
      height: 100%;
      height: 100vh;
      background: darken($bg-color, 5%);
    }
  }

  // left specific styling
  &.left{

    .valet-inner{
      float: left;
    }
  }

  // right specific styling
  &.right{

    .valet-inner{
      float: right;
    }
  }

  // overlay specific styling
  &.overlay{

    .valet-inner{
      float: none;
      margin: $valet-margin*4 auto;
    }
  }

  .valet-inner{
    position: relative;
    background: transparent;
    opacity: 0;
    width: 0;
    margin: 0;
    padding: 0;
    transition: opacity 300ms, width 200ms;
  }
}

body.valet-open > :not(#valet){
  filter: blur(3px);
}